.page {
  background-color: var(--background-main-color);
  width: 100%;
  height: 100vh;
}
.page__img {
  background-image: url('../../images/background-page.jpg');
  background-position: 0px 0px 0px -600px;
  background-repeat: no-repeat;
  background-size: cover;
  width: calc(100% - 600px);
  height: 100vh;
}
.page__img::after {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, .7);
  width: 100%;
  height: 100vh;
}
.page__section {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 600px;
  height: 100vh;
  background-color: var(--background-main-color);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 4px rgb(0 0 0 / 20%);
}
.page__container {
  position: relative;
  padding: 40px 60px;
  width: 100%;
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
}
.page__logo {
  margin: 0px 0px 100px 0px;
  height: 50px;
  object-fit: contain;
  object-position: center;
  user-select: none;
  pointer-events: none;
}
.page__title {
  margin: 0px 0px 50px 0px;
  font-weight: 700;
  font-size: 32px;
  font-style: normal;
  line-height: 1.15;
  text-align: center;
  color: var(--title-color);
}
.page__caption {
  margin: 20px 0px 0px 0px;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  line-height: 1.15;
  text-align: center;
  color: var(--title-color);
  transition: all .3s ease-in-out;
}
.page__caption:hover {
  cursor: pointer;
  color: var(--main-color);
}
.page__contact-list {
  display: flex;
  flex-direction: column;
  padding: 0px;
  list-style-type: none;
  margin: auto 0px 0px 0px;
}
.page__contact-item {
  margin: 0px 0px 15px 0px;
}
.page__contact-item:last-of-type {
  margin: 0px;
}
.page__contact-title {
  margin: 0px 0px 5px 0px;
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  line-height: 1.15;
  text-align: center;
  color: var(--title-color);
}
.page__contact-caption {
  width: 100%;
  margin: 0px;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  line-height: 1.15;
  text-align: center;
  color: var(--title-color);
}